//fonts
@import url(https://fonts.googleapis.com/css?family=Heebo:regular,500,700,900&display=swap);

//variables
$fontFamily: "Heebo";
$minWidth: 320;
$maxWidth: 1152;
$maxWidthContainer: 856;
$md1: $maxWidthContainer + 12;
$md2: 991.98;
$md3: 767.98;
$md4: 479.98;

//reset, common config
@import "null";
@import "blog";
@import "work";
@import "article";

body {
  color: #21243d;
  font-family: $fontFamily;
  font-size: 14px;
  &._lock {
    overflow: hidden;
  }
}

//apadted images
._ibg {
  position: relative;
  img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: cover;
  }
}

body.ei {
  ._ibg {
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }
  ._ibg img {
    width: 0;
    height: 0;
    opacity: 0;
    visibility: hidden;
  }
}

//main block
.page {
  flex: 1 1 auto;
  padding: 90px 0px 0px 0px;
  @media (max-width: $md3+px) {
    padding: 30px 0px 0px 0px;
  }
}

//shell
.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  &._loaded {
  }
}

//main grid
._container {
  max-width: $maxWidthContainer + px;
  margin: 0 auto;
  @media (max-width: $md1+px) {
    max-width: 750px;
  }
  @media (max-width: $md2+px) {
    max-width: none;
    padding: 0 10px;
  }
}

.header {
  position: relative;
  z-index: 100;
  //header__content
  &__content {
    max-width: 1052px;
    padding: 0 10px;
    margin: 0px auto;
  }

  //header__menu
  &__menu {
    height: 82px;
    padding: 10px 0;
    align-items: center;
    display: flex;
    justify-content: flex-end;
    @media (max-width: $md3+px) {
      height: 50px;
    }
  }
}

.menu {
  //.menu__icon
  &__icon {

  }
  //.menu__body
  &__body {
    @media (max-width: $md3+px) {
      transform: translate(0px, -100%);
      transition: transform 0.8s ease 0s; 
      position: fixed;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      overflow: auto;
      padding: 70px 10px 30px 10px;
      background-color: rgba(255, 255, 255, 0.95);
      &._active {
        transform: translate(0px, 0%);
      }
    }
  }
  //.menu__list
  &__list {
    li {
      margin: 0px 0px 30px 0px;
        &:last-child {
          margin: 0px 0px 0px 0px;
        }
    }
    @media (min-width: $md3+px) {
      display: flex;
      li {
        margin: 0px 33px 0px 0px;
      }
    }
  }
  //.menu__link
  &__link {
    font-size: 20px;
    font-weight: 500;
    color: #000;
    transition: color 0.3s ease 0s;
    &._active {
      color: #ff6464;
    }
    @media (min-width: $md2+px) {
      &:hover {
        color: #ff6464
      }
    }
    @media (max-width: $md3+px) {
      font-size: 40px;
      display: block;
      text-align: center;
    }
  }
}

//burger menu
.icon-menu {
  display: none;
  @media (max-width: $md3+px) {
    display: block;
    position: relative;
    width: 30px;
    height: 18px;
    cursor: pointer;
    z-index: 5;

    span {
      transition: all 0.3s ease 0s;
      top: calc(50% - 1px);
      left: 0px;
      position: absolute;
      width: 100%;
      height: 2px;
      background-color: #000;
      &:first-child {
        top: 0px;
      }
      &:last-child {
        top: auto;
        bottom: 0px;
      }
    }

    &._active {
      span {
        transform: scale(0);
        &:first-child {
          transform: rotate(-45deg);
          top: calc(50% - 1px);
        }
        &:last-child {
          transform: rotate(45deg);
          bottom: calc(50% - 1px);
        }
      }
    }
  }
}
//==================================================================
//main content
.hello {
  padding: 38px 0px 70px 0px;
  @media (max-width: $md1+px) {
    padding: 0px 0px 70px 0px;
  }
  @media (max-width: $md3+px) {
    padding: 0px 0px 60px 0px;
  }
  //.hello__container
  &__container {
    display: flex;
    @media (max-width: $md3+px) {
      text-align: center;
      align-items: center;
      flex-direction: column-reverse;
    }
  }
  //.hello__content
  &__content {
    align-items: flex-start;
    padding: 18px 110px 0px 0px;
    flex: 1 1 auto;
    *:last-child {
      margin-bottom: 0;
    }
    @media (max-width: $md1+px) {
      padding: 18px 50px 0px 0px;
    }
    @media (max-width: $md3+px) {
      padding: 0px 0px 0px 0px;
    }
  }
  //.hello__title
  &__title {
    margin: 0px 0px 25px 0px;
    @media (max-width: $md3+px) {
      margin: 0px 0px 20px 0px;
    }
  }
  //.hello__text
  &__text {
    margin: 0px 0px 35px 0px;
    @media (max-width: $md3+px) {
      padding: 0px 15px;
      margin: 0px 0px 25px 0px;
    }
  }
  //.hello__btn
  &__btn {
    margin: 0px 0px 25px 0px;
    @media (max-width: $md3+px) {
      margin: 0px 0px 20px 0px;
    }
  }
  //.hello__avatar
  &__avatar {
    flex: 0 0 243px;
    height: 243px;
    position: relative;
    img {
      max-width: 100%;
    }
    @media (max-width: $md3+px) {
      flex: 0 0 170px;
      width: 170px;
      // height: 170px;
      margin: 0px 0px 40px 0px;
    }
  }
}

.title{
  font-size: 44px;
  font-weight: 700;
  color: #21243d;
  line-height: 60 / 44 * 100%;
  @media (max-width: $md3+px) {
    font-size: 32px;
  }
}

.title-posts {
  font-size: 22px;
  @media (max-width: $md3+px) {
    text-align: center;
    font-size: 18px;
  }
}

.text {
  font-size: 16px;
  line-height: 23 / 16 * 100%;
  color: #21243d;
}

.btn {
  background-color: #ff6464;
  border-radius: 2px;
  font-weight: 500;
  font-size: 20px;
  color: #fff;
  line-height: 29 / 20 * 100;
  height: 47px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0px 20px;
  transition: background-color 0.3s ease 0s;
  @media (min-width: 992px) {
    &:hover {
      background-color: #bb4949;
    }
  }
  &._fw {
    width: 100%;
  }
}

//===================================================================
.recent-posts {
  background: #edf7fa;
  padding: 25px 0px 13px 0px;
  @media (max-width: $md3+px) {
    padding: 25px 0px 23px 0px;
  }
  //.recent-post__container
  &__container {

  }
  //.recent-posts__header
  &__header {
    display: flex;
    margin: 0px 0px 20px 0px;
  }
  //.recent-posts__title 
  &__title {
    padding: 0px 20px 0px 0px;
    flex: 1 1 auto;
    // font-size: 22px;
  }
  //.recent-posts__view-all
  &__view-all {
    flex: 0 0 auto;
    font-size: 16px;
    line-height: 23px;
    color: #00a8cc;
    @media (max-width: $md3+px) {
      display: none;
    }
  }
  //.recent-posts__items
  &__items {
    margin: 0px -10px;
    @media (min-width: $md3+px) {
      display: flex;

    }
  }
  //.recent-posts__column
  &__column {
    flex: 0 1 50%;
    padding: 0px 10px;
    margin: 0px 0px 17px 0px;
  }
}

.recent-post {
  height: 100%;
  background-color: #fff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 4px 10px rgba(187, 225, 250, 0.25);
  border-radius: 4px;
  padding: 20px;
  @media (max-width: $md3+px) {
    padding: 15px;
  }
  *:last-child {
    margin-bottom: 0;
  }
  //.recent-post__title
  &__title {
    display: inline-block;
    color: inherit;
    font-size: 26px;
    font-weight: 700;
    line-height: 38 / 26 * 100%;
    transition: color 0.3s ease 0s;
    margin: 0px 0px 17px 0px;
    @media (min-width: $md2+px) {
      &:hover {
        color: #ff6464;
      }
    }
    @media (max-width: $md3+px) {
      font-size: 22px;
      margin: 0px 0px 10px 0px;
    }
  }
  //.recent-post__info
  &__info {
    margin: 0px 0px 15px 0px;
    font-size: 18px;
    line-height: 26 / 18 * 100%;
    span {
      margin: 0px 15px;
    }
    @media (max-width: $md3+px) {
      font-size: 16px;
      margin: 0px 0px 10px 0px;
    }
  }
  //.recent-post__text
  &__text {
    margin: 0px 0px 15px 0px;
  }
}

//===================================================================
.footer {
  text-align: center;
  padding: 50px 0px;
  @media (max-width: $md3+px) {
    padding: 30px 0px;
  }
  //.footer__social
  &__social {
    margin: 0px 0px 25px 0px;
  }
}

.social {
  display: flex;
  justify-content: center;
  align-items: center;
  //.social__item
  &__item {
    margin: 0px 17px;
    font-size: 30px;
    color: inherit;
    path {
      transition: fill 0.3s ease 0s;
    }
    @media (min-width: $md2+px){
      &:hover {
        path {
          fill: #ff6464;
        }
      }
    }
  }
}
